import React from 'react'
import PropTypes from 'prop-types'

import Home from '@morphoto/home'

import withUploadConnect from './connectors/withUploadConnect'
import withSeriesConnect from './connectors/withSeriesConnect'
import withHomeMap from './connectors/withHomeMap'
import withSearchConnect from './connectors/withSearchConnect'
import withProfileConnect from './connectors/withProfileConnect'
import withFeedConnect from './connectors/withFeedConnect'
import withOfflineData from './connectors/withOfflineData'

import MyFeed from '@morphoto/feed'
import { UploadControl } from '@morphoto/ui'
import Search from '@morphoto/search'
import MyProfile from '@morphoto/profile'
import { HomeMap, OfflineMap } from '@morphoto/maps'
import PhotoSeries from '@morphoto/photoseries'

import {} from '@morphoto/pwainstall'
// import { withFederated } from 'aws-amplify-react'

/**
 * This is the over-arching wrapper for the app. It combines all the
 * same place. Time will tell if that's a good strategy.
 */
const App = ({ installPrompt, ...props }) => {
  // We pass in the components separately so that it's easier to mock them up
  // without data connectors in cosmos
  const components = {
    upload: withUploadConnect(UploadControl),
    series: withSeriesConnect(PhotoSeries),
    map: withHomeMap(HomeMap),
    search: withSearchConnect(Search),
    profile: withProfileConnect(MyProfile),
    feed: withFeedConnect(MyFeed),
    offline: withOfflineData(OfflineMap),
    pwaInstall: props.installPrompt
  }

  return (
    <Home {...props} components={components} />
  )
}

App.propTypes = {
  installPrompt: PropTypes.any
}

export default App
