import React, { useRef, useEffect, useContext, useMemo, useState } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import { graphql, compose } from 'react-apollo'

import { AppContext } from '@morphoto/client-common'
import datastore from '@morphoto/datastore'

const withUploadConnect = Wrapped => {
  const Inner = ({
    activeUpload,
    completeActiveUpload,
    updateUploadProgress,
    ...props
  }) => {
    const uploadingRef = useRef()
    const [qItem, setQItem] = useState()
    const { online } = useContext(AppContext)

    uploadingRef.current = activeUpload ? `${activeUpload.id}__retry${activeUpload.retry}` : null

    const newS3 = useMemo(() => <NewS3Credentials onDone={updateUploadProgress} qItem={qItem} />, [qItem, updateUploadProgress])

    useEffect(() => {
      console.log('UPLOADDEBUG::::', { uploadingRef, activeUpload, online })
      if (online.isOnline && activeUpload && uploadingRef.current) {
      // if the upload fails the failed qItem gets set in the state
        datastore.s3Upload(activeUpload, updateUploadProgress, completeActiveUpload, setQItem)
      }
    }, [activeUpload, completeActiveUpload, online, updateUploadProgress])

    if (!activeUpload) return null
    return (
      <React.Fragment>
        {newS3}
        <Wrapped text={'Uploading image'} progress={activeUpload.progress || 0} {...props} />
      </React.Fragment>
    )
  }
  Inner.propTypes = {
  // All from redux
    activeUpload: PropTypes.object,
    completeActiveUpload: PropTypes.func.isRequired,
    updateUploadProgress: PropTypes.func.isRequired
  }

  return Inner
}

/**
 * Here's the graphql HOC that gets an individual series
 */
const withGetNewS3Credentials = graphql(datastore.graphql.queries.getNewS3Credentials, {
  skip: ({ online, qItem }) => !online || !qItem,
  props: ({ data, ownProps }) => {
    if (data.error) console.error(data.error)
    console.log('getWholeSeries:: ', { data })

    // If we get backa  valid auth code then create a new queue object
    // and send it back
    if (!data.loading) {
      ownProps.onDone({
        ...ownProps.qItem,
        retry: ownProps.qItem.retry + 1,
        preAuth: JSON.parse(data.s3Credentials)
      })
    }
    return {
      data
    }
  },
  options: ({ qItem }) => ({
    variables: { seriesId: qItem.seriesId, photoId: qItem.id, ext: qItem.ext }
  })
})
const NewS3Credentials = withGetNewS3Credentials(() => null)

/**
 * Map our redux state and functions
 */
const mapStateToProps = (state, props) => {
  const { activeUpload } = state.uploader
  return { activeUpload }
}

const mapDispatchToProps = dispatch => ({
  completeActiveUpload: () => {
    dispatch(datastore.redux.actions.completeActiveUpload())
  },
  updateUploadProgress: (newProgress) => {
    dispatch(datastore.redux.actions.updateUploadProgress(newProgress))
  }
})

export default compose(
  connect(
    mapStateToProps,
    mapDispatchToProps
  ),
  withUploadConnect
)
